import { Component, OnInit } from '@angular/core';
import { SearchService } from '../services/search.service';
import { ProductTypeService } from '../services/product-type.service';
import { UserService } from '../services/user.service';
import { ProductsService } from '../services/products.service';
import { ProducersService } from '../services/producers.service';
import { RegisterService } from '../services/register.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-pretraga',
  templateUrl: './pretraga.component.html',
  styleUrls: ['./pretraga.component.css']
})
export class PretragaComponent implements OnInit {


  constructor(private userService: UserService,
              private registerService: RegisterService,
              private productService: ProductsService,
              private producersService: ProducersService,
              private searchService: SearchService,
              private productTypeService: ProductTypeService,
              private http: HttpClient,
              private router: Router) { }
  pozicija : string = localStorage.getItem('username');
  rola : string = localStorage.getItem("role")

  endpointURL5 : string = 'http://78.46.117.245:9000/logout'
  headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8',
                             'token' : localStorage.getItem("token")});
  
  routerVar : string;
  statsVar : boolean;
  helpText = "";

  ngOnInit() {

    if(this.rola == 'ADMIN' || this.rola == 'OWNER'){
      this.routerVar = '/admin'; 
      this.statsVar = true;
      this.helpText=`

Početna

* Na početnoj strani su izlistani svi proizvodi koji su trenutno dostupni u prodavnici. Moguće je promeniti bilo koji atribut postojaćeg proizvoda, moguće je ukloniti proizvod, a takođe i izvršiti popust unošenjem odgovarajućeg iznosa procenta. Na crvenu strelicu se proizvod ubacuje u korpu. 


Spisak radnika

* Omogućen je uvid u spisak svih radnika koji rade u magacinu Techtradea kao i njihove informacije. Moguće je ukloniti korisnika.


Naručivanje proizvoda

* Na početku su izlistani svi proizvodi svih dobavljača. Prilikom narudžbenice, moguće je u korpu dodati proizvode pritiskom na crvenu strelicu. U korpi željeni kvantitet unosi sam korisnik. Moguće je obrisati sadržaj iz korpe. Pritiskom na dugme za kupovinu potvrđuje se porudzbina. 


Naručeni proizvodi

* Omogućen je uvid u dve tabele, Naručeni proizvodi koji treba da stignu i Naručeni proizvodi koji su stigli.


Zameni proizvod

* Izvršiti odabir šifre računa;
* Izvršiti odabir šifre proizvoda na računu;
* Uneti šifru željenog proizvoda za zamenu;
* Pritiskom na dugme 'Zameni' izbaciće se odgovarajuća poruka.


Registracija radnika

* Omogućava unošenje novih autorizovanih korisnika. Unošenjem svih potrebnih podataka, kao i podatka koji autorizovani korisnik je u pitanju, biće izbačena odgovarajuća poruka.


Dodavanje proizvoda

* Moguće je dodati nov proizvod. U slučaju da proizvod već postoji, biće izbačena odgovarajuća poruka.


Odredi popust

* Odabrati proizvođača;
* Odabrati željeni proizvod;
* Uneti iznos procenta;
* Pritiskom na dugme 'Izvrši', biće izbačena odgovarajuća poruka.


Automatski popust

* Omogućen je uvid u spisak svih proizvoda kojima je omogućen automatski popust i potvrda popusta.


Prikaz računa

* Omogućen je uvid u tabelu sa računima;
* Moguće je izvesti račun koji je generisan u PDF formi.


Prikaz fakture

* Omogućen je uvid u tabelu faktura;
* Moguće je izvesti fakturu u PDF formi.
`
    }
    else if (this.rola == 'MANAGER'){
      this.routerVar = '/komercijalista';
      this.helpText=`


Početna

* Na početnoj strani su izlistani svi proizvodi svih dobavljača. Moguće je izvršiti izmenu cene željenog proizvoda.


Naručivanje proizvoda

* Kada radnik naruči proizvod, komercijalista će to videti i ti proizvodi će se pojaviti u tabeli. U narudzbenici svi proizvodi moraju biti od istog dobavljača. Komercijalista bira proizvode koje treba da naruči od dobavljača i njihov kvantitet. 


Naručeni proizvodi

* Omogućen je uvid u dve tabele, Naručeni proizvodi koji treba da stignu i Naručeni proizvodi koji su stigli.


Prikaz fakture

* Omogućen je uvid u tabelu faktura;
* Moguće je izvesti fakturu u PDF formi.


Automatski popust

* Omogućen je uvid u spisak svih proizvoda kojima je omogućen automatski popust i potvrda popusta.

`
    }
    else if (this.rola == 'WORKER'){
      this.routerVar = '/radnik';
      this.helpText="* Radnik ima mogućnost da prodaje proizvod, vrši zamenu proizvoda kao i da naruči proizvode kojih nema na stanju. Kada radnik izabere proizvod koji hoće da proda, klikom na crvenu strelicu dodaje taj proizvod u korpu. Prečicom u gornjem desnom uglu se pristupa korpi. Nakon odabira količine proizvoda kojeg korisnik želi da poruči, pritiskom na dugme Izvrši kupivinu potvrđuje se porudzbina. Moguće je obrisati sadržaj iz korpe. Radnik naručuje proizvode kojih nema na stanju i svi proizvodi se šalju komercijalisti i on ih obrađuje.Zamena proizvoda se izvršava nakon odabira šifre računa, šifre proizvoda na računu, a zatim i šifre željenog proizvoda za zamenu. Pritiskom na dugme 'Zameni' izbaciće se odgovarajuća poruka.";
    }



  }
  korpa = JSON.parse(localStorage.getItem("cart"));

  logout(){
    this.http.post(this.endpointURL5,{"logout": "logout"}, {headers:this.headers}).subscribe(status => {

      if(status){
        this.router.navigate([""]);
        localStorage.removeItem("token");
      }
    })
  }
 

  addToCart(event){
    const id = event.srcElement.id;

    if(this.korpa == null || this.korpa.length == 0){
      this.korpa = [];
      this.korpa.push(id);
    }
    else{

      if(this.korpa.includes(id.toString())){
        alert('Proizvod je vec ubacen u korpu')
      }
      else{
        this.korpa.push(id)
      }

    }


    localStorage.setItem("cart", JSON.stringify(this.korpa));
    
  }
  
  handleError(error) {
    if(error.status != 200){

        alert(error.error.message);

    }
  }

}
